const state = {
     beforei:"",
     beforen:""
  }
  //
  const getters={
  
  }
  //
  const mutations = {
   
    changeNavi(state,res){
      state.beforei=res
    },
    changeNavn(state,res){
        state.beforen=res
      }
  }
  // 
  const actions={
    CHANGEI(context,res){
        context.commit('changeNavi',res)
     },
     CHANGEN(context,res){
        context.commit('changeNavn',res)
     }
  }
  // 
  export default {
    namespaced: true,
  // 
    state,
    getters,
    actions,
    mutations
  }